import React from "react";
import { Grid, Icon, Header, Divider } from "semantic-ui-react";

const Interest = () => (
  <div align='center'>
    <Grid doubling columns={3}>
      <Grid.Column>
        <Icon name="tree" color="green" inverted size="large" circular />
        <Header as="h4">Sport Adventure</Header>
      </Grid.Column>
      <Grid.Column>
        <Icon name="music" color="teal" inverted size="large" circular />
        <Header as="h4">Music</Header>
      </Grid.Column>
      <Grid.Column>
        <Icon name="camera retro" color="red" inverted size="large" circular />
        <Header as="h4">Photography</Header>
      </Grid.Column>
      {/* </Grid>
        <Grid doubling columns={3}> */}
      <Grid.Column>
        <Icon name="futbol" color="grey" inverted size="large" circular />
        <Header as="h4">Football</Header>
      </Grid.Column>
      <Grid.Column>
        <Icon name="camera" color="pink" inverted size="large" circular />
        <Header as="h4">Videography</Header>
      </Grid.Column>
      <Grid.Column>
        <Icon name="globe" color="violet" inverted size="large" circular />
        <Header as="h4">Web development</Header>
      </Grid.Column>
    </Grid>
    <Divider hidden />
    <Divider hidden />
    <Divider hidden />
    <Divider hidden />
    <Divider hidden />
    <Divider hidden />
    <Divider hidden />
    <Divider hidden />
    <Divider hidden />
  </div>
);

export default Interest;



